@font-face {
  font-family: GothamPro;
  src: url("../fonts/GothamProLight.ttf"), url("../fonts/GothamProLight.eot"), url("../fonts/GothamProLight.woff");
  font-weight: 300;
}
@font-face {
  font-family: GothamPro;
  src: url("../fonts/GothamProRegular.ttf"), url("../fonts/GothamProRegular.eot"), url("../fonts/GothamProRegular.woff");
  font-weight: 400;
}
@font-face {
  font-family: GothamPro;
  src: url("../fonts/GothamProMedium.ttf"), url("../fonts/GothamProMedium.eot"), url("../fonts/GothamProMedium.woff");
  font-weight: 500;
}
@font-face {
  font-family: GothamPro;
  src: url("../fonts/GothamProBold.ttf"), url("../fonts/GothamProBold.eot"), url("../fonts/GothamProBold.woff");
  font-weight: 700;
}
@font-face {
  font-family: GothamPro;
  src: url("../fonts/GothamProBlack.ttf"), url("../fonts/GothamProBlack.eot"), url("../fonts/GothamProBlack.woff");
  font-weight: 900;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
html {
  font-family: 'Roboto', sans-serif;
}
body {
  padding: 20px;
}
.friend-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.friend-list-wrapper {
  width: 400px;
  margin: 0 auto;
  box-shadow: rgba(0,0,0,0.25) 0 14px 20px, rgba(0,0,0,0.22) 0 10px 18px;
}
.friend-list__title {
  background-color: #00bcd4;
  color: #fff;
  padding: 10px;
  text-transform: uppercase;
  font-weight: 500;
}
.friend-list__item {
  position: relative;
  display: flex;
  padding: 20px;
  border-bottom: 1px solid #e8e8e8;
}
.friend-list__item:hover .friend-list__remove,
.friend-list__item:focus .friend-list__remove {
  opacity: 1;
}
.friend-list__data {
  padding: 20px;
  padding-right: 0;
}
.friend-list__name {
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: 500;
  color: #797979;
}
.friend-list__img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.friend-list__search-field {
  font-family: Roboto;
  font-size: 16px;
  width: 100%;
  padding: 10px;
  border: none;
  border-bottom: 1px solid #e8e8e8;
  outline: none;
}
.friend-list__search-field+.input-animate {
  height: 1px;
  position: relative;
  margin-top: -1px;
}
.friend-list__search-field+.input-animate::after {
  content: '';
  height: 1px;
  position: absolute;
  left: 0;
  width: 0;
  background-color: #00bcd4;
  transition: 300ms width;
}
.friend-list__search-field:focus +.input-animate::after {
  width: 100%;
  transition: 300ms width;
}
.friend-list__remove {
  background: transparent;
  border: none;
  outline: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  opacity: 0;
  transition: 200ms opacity;
}
.friend-list__remove::before,
.friend-list__remove::after {
  position: absolute;
  content: '';
  display: block;
  height: 15px;
  width: 2px;
  background-color: #000;
  transform-origin: center;
}
.friend-list__remove::before {
  transform: rotate(45deg);
}
.friend-list__remove::after {
  transform: rotate(-45deg);
}
.friend-list__tel {
  text-decoration: none;
  color: #009587;
}
.friend-list__tel:hover,
.friend-list__tel:focus {
  text-decoration: underline;
}
